import React, { useEffect, useRef, memo } from "react";
import loadScript from 'load-script';

function TradingViewWidget() {
  const container = useRef();

  useEffect(() => {
    const containerCurrent = container.current;

    // Load the TradingView script dynamically
    loadScript(
      'https://s3.tradingview.com/external-embedding/embed-widget-symbol-overview.js',
      (err, script) => {
        if (err) {
          console.error('Failed to load TradingView script:', err);
          return;
        }

        // The script is loaded successfully
        script.innerHTML = `{
          "symbols": [
            ["NASDAQ:NKTR|ALL"],
            ["NASDAQ:GOEV|ALL"],
            ["NASDAQ:GCT|ALL"]
          ],
          "chartOnly": false,
          "width": "100%",
          "height": "100%",
          "locale": "en",
          "colorTheme": "dark",
          "autosize": true,
          "showVolume": false,
          "showMA": false,
          "hideDateRanges": false,
          "hideMarketStatus": false,
          "hideSymbolLogo": false,
          "scalePosition": "right",
          "scaleMode": "Normal",
          "fontFamily": "-apple-system, BlinkMacSystemFont, Trebuchet MS, Roboto, Ubuntu, sans-serif",
          "fontSize": "10",
          "noTimeScale": false,
          "valuesTracking": "1",
          "changeMode": "price-and-percent",
          "chartType": "area",
          "maLineColor": "#2962FF",
          "maLineWidth": 1,
          "maLength": 9,
          "headerFontSize": "medium",
          "lineWidth": 2,
          "lineType": 0,
          "dateRanges": ["1d|1", "1m|30", "3m|60", "12m|1D", "60m|1W", "all|1M"]
        }`;

        containerCurrent.appendChild(script);
      }
    );

    // Clean up: remove the script on unmount
    return () => {
      containerCurrent.innerHTML = "";
    };
  }, []);

  return (
    <div className="tradingview-widget-container" ref={container}>
      <div className="tradingview-widget-container__widget"></div>
      <div className="tradingview-widget-copyright">
        <a
          href="https://www.tradingview.com/"
          rel="noopener nofollow"
          target="_blank"
        >
          <span className="blue-text">Track all markets on TradingView</span>
        </a>
      </div>
    </div>
  );
}

export default memo(TradingViewWidget);
